<template>
  <div class="mod-back" @click="isEmit ? handleClick() : exitClick()">
    <img src="./img/back.png" class="back-img"/> 返回
  </div>
</template>
<script>
import { setStore,getStore } from '@/assets/js/utils'
import { config } from '@/assets/js/config'

export default {
  props:{
    isEmit:{  // 是否emit事件
      type:Boolean,
      default:false
    }
  },
  data(){
    return{
      uid:getStore(config.USERID),
      isSubmit: getStore(config.STATUS) == 1
    }
  },
  methods:{
    handleClick(){
      this.$emit('handleClick')
    },
    exitClick(){
      // 设置评测状态
      if(!this.isSubmit){
        setStore(config.STATUS, 2)
        // 退出提示
        let $msg = this.$messagebox({
          text:'确定要退出测评？退出后未完成测评将会为你保存',
          onClose:()=>{
            this.$router.replace({name:'home'})
          }
        })
      }else{
        this.$router.replace({name:'home'})
      }
    }
  }
}
</script>
<style lang='scss' scoped>
@import "@/assets/style/variable.scss";
.mod-back{
  display: flex;
  justify-content: center;
  align-items: center;
  width:112px;
  height:54px;
  background-color:rgba(0,0,0,.5);
  border-radius: 0 54px 54px 0;
  font-size:24px;
  color:$cl-white;
  margin:10px 0;
  .back-img{
    width:14px;
    margin-right:10px;
  }
}
</style>


