<template>
  <div class="page-container">
    <header-back />
    <div class="article-bd">
      <div class="header">
        <div class="time-box" v-if="!isSubmit">
          <img src="./img/time.png" alt="time" class="time-img">时间：{{formatUserTime(usertime)}}
        </div>
        <div class="time-box" v-else></div>
        <div class="card-box" @click="cardVisible = !cardVisible">答题卡 <span class="t-light">{{currentIndex}}/{{subjectDatas.length}}</span></div>
      </div>
      <div class="section" :class="{'sub-section':isSubmit}" v-for="(v,k) in subjectDatas" :key="k">
        <h3 class="t-light category" v-if="currentIndex == v.order">{{v.typeName}}</h3>
        <div class="wrap-scroll" :class="{'sub-scroll':!isSubmit}" v-if="currentIndex == v.order">
          <h4 class="title" v-html="replaceText(v.title)"></h4>
          <div class="items" v-for="(m, n, x) in v.option" :key="x" @click="checkIn(v,n)">
            <em :class="{active:!isSubmit && v.uanswer == n,error:isSubmit && v.uanswer == n,correct:isSubmit && v.answer == n }">{{n}}</em>
            <span v-html="replaceText(m)"></span>
          </div>
          <div class="bd-section" v-if="isSubmit">
            <div class="answer">正确答案是<span class="t-correct">{{v.answer}}</span>，你的答案是<span class="t-user">{{v.uanswer}}</span></div>
            <div class="message">
              <h4 class="hd-message">答案解析</h4>
              <p v-html="replaceText(v.analysis)"></p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="article-fd">
      <div class="items" :class="{none:currentIndex == 1}" @click="currentIndex > 1 && currentIndex--">上一题</div>
      <div class="items" @click="(currentIndex < subjectDatas.length) && currentIndex++" v-show="currentIndex < subjectDatas.length">下一题</div>
      <div class="items last" @click="shareTo" v-show="!isSubmit && currentIndex == subjectDatas.length">提交评测</div>
    </div>
    <!-- 答题卡 -->
    <h-card class="m-aside" 
      :isSubmit="isSubmit" 
      :cardDatas="subjectDatas" 
      :cardVisible.sync="cardVisible" 
      @handleCheck="checkTo"
      @handleSubject="subjectTo"
      @handleSubmit="shareTo">
    </h-card>
  </div>
</template>

<script>
import HeaderBack from '@/components/HeaderBack'
import HCard from './components/Card'

import { Subject } from '@/assets/api/subject'
import { replaceContent,debounce,formatTime,setStore,getStore,removeStore } from '@/assets/js/utils'
import { config } from '@/assets/js/config'

export default {
  name: 'subject',
  components: {
    HeaderBack,
    HCard
  },
  data () {
    return {
      uid:getStore(config.USERID),
      isSubmit:getStore(config.STATUS) == 1,     // 是否已测评
      cardVisible:false,  // 是否显示答题卡
      subjectDatas:[],  // 所有题目数据
      answerList:[],    // 用户作答数据
      currentIndex:1,   // 当前题目索引
      timer:null,       // 定时器
      usertime:0       // 答题用时
    }
  },
  created(){
    this.$nextTick(()=>{
      this._getSubject()
    })
  },
  methods:{
    // 获取题目
    _getSubject(){
      Subject.getSubject({
        userId: this.uid
      }).then(res=>{
        if(res.code == 1){
          this.subjectDatas = res.data
          this.tempStorage = this.subjectDatas
          if(!this.isSubmit){
            // 初始化作答
            this._updateLocalAnswer(true)
            // 根据本次存储初始化计时器
            this._initLocalDatas()
            this._startTimer()
          }       
        }
      })
    },
    // 文本换行替换
		replaceText(text){
			return replaceContent(text)
    },
    // 秒数格式化为时分秒
		formatUserTime(time) {
			return formatTime(time)
    },
    // 单选 选中
    checkIn(v,n){
      if(this.isSubmit) return
      setStore(config.STATUS, 2)
      this.$set(v,'uanswer',n)
      this._updateLocalAnswer()
      this._submitAnswer(true)  // 临时存储
      if(this.currentIndex < this.subjectDatas.length){
        this.currentIndex++
      }
    },
    // 计时器启动
		_startTimer() {
			this.timer = setInterval(() => {
        this.usertime++ 
        setStore(config.TIMER, this.usertime)     
			}, 1000)
    },
    // 暂停计时器
		_pauseTimer() {
			clearInterval(this.timer)
    },
    // 答题卡切换题目
    checkTo(index){
      this.cardVisible = false
      this.currentIndex = index
    },
    // 0查看错题 | 1全部解析
    subjectTo(type){
      if(!this.isSubmit) return
      this.cardVisible = false
      let datas = []      
      if(type == 0){
        this.tempStorage.forEach((v,k)=>{
          if(v.uanswer != v.answer){  // 错题筛选
            datas.push(v)
          }
        })
        this.subjectDatas = datas
      }else{
        this.subjectDatas = this.tempStorage
      }
      this.currentIndex = 1
    },
    // 更新本地作答
    _updateLocalAnswer(onload){
      // onload 是否初始化加载第几题
      this.onload = onload
      this.answerList = []
      this.subjectDatas.forEach((v,k) => {
        if(!!v.uanswer){
          this.answerList.push({
            id:v.id,
            uanswer:v.uanswer
          })
        }else if(this.answerList.length > 0 && this.onload){
          this.onload = false
          this.currentIndex = k + 1
        }
      })
      setStore(config.ANSWER,JSON.stringify(this.answerList))
    },
		// 根据本地存储初始化数据
		_initLocalDatas() {
      let times = getStore(config.TIMER)
			if (!this.isSubmit) {
        this.usertime = times ? JSON.parse(times) : 0
			}
    },
		// 提交答案
		_submitAnswer(temp){
      if(!getStore(config.ANSWER)) return
      // temp 标识临时存储 true，提交测评 空
			Subject.submitSubject({
        userId: this.uid,
        result: getStore(config.ANSWER),
        type: temp ? 0 : 1    // 0存储  1提交
      }).then(res=>{
        if(res.code == 1 && !temp){
          this._pauseTimer()
          removeStore(config.ANSWER)
          removeStore(config.TIMER)
          setStore(config.STATUS, 1)
          this.$router.replace({name:'share'})
        }
      })
		},
    // 提测
    shareTo(){
      if(this.answerList.length < this.subjectDatas.length){
        this.$notify({ content: "请完成所有的题目再交卷哦~" })
      }else{
        debounce(this._submitAnswer(),1000)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/style/variable.scss";
.article-bd{
  .t-light{
    color:#999;
  }
  .header{
    display: flex;
    align-items: center;
    padding:0 30px;
    height: 80px;    
    background-color:$cl-theme-ll;
    .time-box{
      flex:1;
      display: flex;
      align-items: center;
    }
    .time-img{
      width:34px;
      margin-right:15px;
    }
  }
  .wrap-scroll{
    position:absolute;
    bottom:110px;
    left:0;
    width:100%;
    height: calc(100vh - 74px - 80px - 110px);
    padding-bottom:40px;
    box-sizing: border-box;
    overflow: auto;
    &.sub-scroll{
      height: calc(100vh - 68px - 74px - 80px - 110px);
    }
  }
  .section{
    margin-top:30px;
    .category{
      font-size:28px;
      margin:0 28px 30px;
    }
    .title{
      margin:0 35px;
      margin-bottom:30px;
      line-height: 1.8;      
    }
    .items{
      margin:0 48px;
      padding-left:60px;
      box-sizing: border-box;
      font-size:28px;
      line-height: 48px;
      margin-bottom:36px;
      position: relative;
      em{
        position:absolute;
        left:0;
        top:0;
        width:48px;
        height:48px;
        margin-right:23px;
        text-align: center;
        line-height: 48px;
        border-radius: 100%;
        background-color:#b0b0b0;
        font-size:24px;
        color:$cl-white;
        &.active{
          background-color:$cl-theme;
        }
        &.error{
          background-color:#ffac0d;
        }
        &.correct{
          background-color:#61c797;
        }
      }
    }
    .bd-section{
      margin:0 35px;
      font-size:30px;
      .answer{
        height:60px;
        line-height: 60px;
        .t-correct{
          color:#61c797;
        }
        .t-user{
          color:#ffac0d;
        }
      }
      .message{
        font-size:28px;
        padding:28px;
        border:2px solid #eee;
        box-shadow: 1px 5px 8px rgba(0,0,0,.1);
        .hd-message{
          padding-left:30px;
          font-size:30px;
          font-weight: 600;
          margin-bottom:20px;
          position: relative;
          &::before{
            content:'';
            position:absolute;
            top:8px;
            left:0;
            width:8px;
            height: 30px;
            border-radius: 8px;
            background-color:$cl-theme;
          }
        }
      }     
    }
    &.sub-section{
      margin-top:-80px;
      .category{
        height:80px;
        line-height: 80px;
      }
    }

  }
}
.article-fd{
  position:absolute;
  bottom:0;
  left:0;
  width:100%;
  height:110px;
  background-color:$cl-bgcolor;
  display: flex;
  justify-content:space-around;
  align-items: center;
  .items{    
    width:282px;
    height: 74px;
    text-align:center;
    line-height: 74px;
    border-radius: 74px;
    border:2px solid $cl-theme;
    box-sizing: border-box;
    color:$cl-theme;
    &.none{
      border-color:#aaa;
      color:#aaa;
    }
    &.last{
      background-color:$cl-theme;
      color:$cl-white;
    }
  }
}
.m-aside{
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height: 100%;
}
</style>
<style>
.article-bd .title img{ width:80%; }
</style>


