<template>
  <transition name="outin-transform">
    <div class="page-container" v-show="cardVisible">
      <header-back :isEmit="true" @handleClick="closedCard"/>
      <div class="card-hd" v-if="isSubmit">
        <p>答对</p>
        <p class="desc"><em>{{correctNum}}</em>题/{{cardDatas && cardDatas.length}}题</p>
      </div>
      <div class="card-bd" v-if="cardDatas">
        <span 
        v-for="(v,k) in cardDatas"
        :key="k" 
        :class="{active:!isSubmit && v.uanswer,correct:isSubmit && v.uanswer === v.answer,error:isSubmit && v.uanswer != v.answer }"
        @click="checkIndex(k+1)">
          {{k+1}}
        </span>
      </div>
      <!-- 答题中 -->
      <div class="card-fd" v-if="!isSubmit">
        <div class="items next" @click="handleSubmit">提交测评并查看结果</div>
      </div>
      <!-- 已评测 -->
      <div class="card-fd" v-else>
        <div class="items prev" @click="subjectTo(0)">错题解析</div>
        <div class="items next" @click="subjectTo(1)">全部解析</div>
      </div>
    </div>
  </transition>
</template>
<script>
import HeaderBack from '@/components/HeaderBack'
import { getStore } from '@/assets/js/utils'
import { config } from '@/assets/js/config'

export default {
  name: 'cards',
  components: {
    HeaderBack
  },
  props:{
    cardVisible:{
      type:Boolean,
      default:false
    },
    isSubmit:{
      type:Boolean,
      default:false,
    },
    cardDatas:{
      type:Array,
      default:()=>[],
    } 
  },
  computed:{
    correctNum(){
      let number = 0
      this.cardDatas.forEach(v => {
        if(v.uanswer == v.answer){
          number++
        }
      });
      return number
    }
  },
  methods:{
    // 答题卡切换题目
    checkIndex(index){
      this.$emit('handleCheck',index)
    },
    // 查看解析
    subjectTo(type){
      // type 0 错题 1 全部
      this.$emit('handleSubject', type)      
    },
    // 关闭答题卡
    closedCard(){
      this.$emit('update:cardVisible', false)
    },
    // 提测
    handleSubmit(){
      this.$emit('handleSubmit')
    }
  }
}
</script>
<style lang='scss' scoped>
@import "@/assets/style/variable.scss";
.card-hd{
  text-align: center;
  color:#333;
  .desc{
    font-size:24px;
    em{
      font-size:48px;
      color:#61c797;
    }
  }
}
.card-bd{
  display: flex;
  flex-direction: row; 
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin-top:50px;
  margin-left:56px;
  span{
    display: inline-block;
    width:82px;
    height: 82px;
    margin-right:56px;
    margin-bottom:56px;
    text-align:center;
    line-height: 82px;
    border-radius: 100%;
    background-color:#b0b0b0;
    color:$cl-white;
    font-size:38px;
    &.active{
      background-color:$cl-theme;
    }
    &.error{
      background-color:#ffac0d;
    }
    &.correct{
      background-color:#61c797;
    }
  }
}
.card-fd{
  position:absolute;
  bottom:0;
  left:0;
  width:100%;
  height: 100px;
  display: flex;
  .items{
    flex:1;
    height: 100px;
    text-align:center;
    line-height: 100px;
    color:$cl-white;
    font-size:36px;
  }
  .prev{
    background-color:$cl-theme-l;
  }
  .next{
    background-color:$cl-theme;
  }
}
</style>


